import React, { Component } from 'react';
import Countries from './components/Countries';
import Container from '@material-ui/core/Container';
import Badge from '@material-ui/core/Badge';
import { withStyles } from '@material-ui/core/styles';
import './App.css';

const styles = {
  appHeading: {
    textAlign: 'center',
    marginTop: '15px',
  },
  appHeadingBadge: {
    fontSize: '2.0em',
    fontFamily: 'Teko, sans-serif',
  },
  appHeadingText: {
    paddingRight: '8px'
  }
}

class App extends Component {
  state = {
    countries: [
      { id: 1, name: 'United States', goldMedalCount: 2, silverMedalCount: 2, bronzeMedalCount: 3 },
      { id: 2, name: 'China', goldMedalCount: 3, silverMedalCount: 1, bronzeMedalCount: 0 },
      { id: 3, name: 'Germany', goldMedalCount: 0, silverMedalCount: 2, bronzeMedalCount: 2 },
    ],
    medals: [
      { id: 1, name: 'gold', RGBColor: '#ffc107' },
      { id: 2, name: 'silver', RGBColor: '#c4cacc' },
      { id: 3, name: 'bronze', RGBColor: '#cd7f32' },
    ]
  }
  handleIncrement = (countryId, medal) => {
    const countries = [ ...this.state.countries ];
    const idx = countries.findIndex(c => c.id === countryId);
    countries[idx][medal + 'MedalCount'] += 1;
    this.setState({ countries: countries });
  }
  handleDecrement = (countryId, medal) => {
    const countries = [ ...this.state.countries ];
    const idx = countries.findIndex(c => c.id === countryId);
    if (countries[idx][medal + 'MedalCount'] === 0) return;
    countries[idx][medal + 'MedalCount'] -= 1;
    this.setState({ countries: countries });
  }
  getMedalTotal = (medal) => this.state.countries.reduce((a, b) => a + b[medal + 'MedalCount'], 0);
  getAllMedalsTotal() {
    let sum = 0;
    this.state.medals.forEach(medal => {
      sum += this.getMedalTotal(medal.name);
    });
    return sum;
  }
  render() { 
    const {classes} = this.props;
    return (
      <Container>
        <React.Fragment>
          <div className={classes.appHeading}>
            <Badge 
              className={classes.appHeadingBadge}
              badgeContent={ this.getAllMedalsTotal() } 
              color='primary'>
              <div className={classes.appHeadingText}>Olympic Medals</div>
            </Badge>
          </div>
          <Countries 
            countries={ this.state.countries } 
            medals={ this.state.medals } 
            onIncrement={ this.handleIncrement } 
            onDecrement={ this.handleDecrement } />
        </React.Fragment>
      </Container>
    );
  }
}
 
export default withStyles(styles, { withTheme: true })(App);
